.label {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  width: 250px;
}
.title {
  text-align: center;
  margin-bottom: 20px;
}
.input {
  display: block;
  width: 100%;
  border: none;
  border-bottom: 2px solid #008cba;
  padding: 10px;
  transition: 0.3s;
}
